.cards {
    height: 70%;
    display: flex;
    justify-content: space-between;
}

.card {
    width: 20%;
    height: 100%;
}

.box_title_card {
    height: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}

.ava {
    width: 50%;
    height: 100px;
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100%;
}

.title {
    font-size: 1rem;
}

.description {
    word-break: break-word;
}

@media all and  (max-width: 768px) {
    .cards {
        flex-direction: column;
        align-items: center;
        gap: 30px;
    }
    .card{
        width: 90%;
    }
    .ava {
        width: 20%;
    }
}